@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,100..700;1,100..700&display=swap');

body {
  font-family: "Josefin Sans", sans-serif;
  height: 100vh;
  margin: 0; 
  padding: 0; 
}

h1 {
  font-size: 60px;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 20px;
}
span {
  font-weight: 300;
  color: hsl(0, 42%, 61%);
}

p {
  color: hsl(0, 42%, 61%);
  font-size: 18px;
  line-height: 28px;
}

.main {
  display: flex;
  width: 100%;
  /* justify-content: space-between; */
  color: hsl(0, 9%, 13%);
  box-sizing: border-box;
}

.hero-mobile {
  display: none;
}

.content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;

  width: 58%;
  background: url("./images/bg-pattern-desktop.svg");
  background-repeat: repeat-x;
  box-sizing: border-box;
  padding: 6rem 15rem 6rem 10rem;
}

.hero-img {
  height: 100vh;
  width: 42%;
} 

.hero-img > img {
  object-fit: cover;
  width: 100%; 
  height: 100vh; 
}

.circle {
  background: linear-gradient(white, white) padding-box,
              linear-gradient(to right, hsl(0, 80%, 86%), hsl(0, 74%, 74%)) border-box;
  border-radius: 50em;
  border: 4px solid transparent;

  width: 40px;
  height: 40px;

  margin-right: 1rem;
}

.logo-block {
  margin-bottom: 6rem;
}

.logo-block, .logo-block-mobile {
  display: flex;
  align-items: center;
}

.logo-block-mobile {
  display: none;
}

.logo-block p, .logo-block-mobile p {
  font-weight: bold;
  letter-spacing: 2px;
  font-size: 20px;
  line-height: 24px;
  color: hsl(0, 9%, 13%);

}

form {
  display: flex;
  margin-top: 3rem;
  position: relative;
}

form > input:nth-child(1) {
  border: 1px solid hsla(0, 42%, 61%, 0.415);
  border-radius: 40px;

  display: flex;
  justify-content: center;
  align-items: center;

  box-sizing: border-box;
  padding: 1rem;
  width: 100%;
}

.input-button  {
  min-height: 100%;
  width: 100px;

  border-radius: 40px;
  margin-left: -5rem;
  background: linear-gradient(to left, hsl(0, 74%, 74%), hsl(0, 80%, 86%));
  filter: drop-shadow(0px 10px 8px #2f2f2f35);
  /* transition: all 1s ease; */
  transition: all 0.5s ease;
  cursor: pointer;
}

.input-button > input {
  height: 100%;
  border: none;
  width: 100px;

  border-radius: 40px;
  background-color: transparent;
  background: url("./images/icon-arrow.svg");
  background-repeat: no-repeat;
  background-position: center;

  transition: all 0.5s ease;
}

.input-button:hover  {
  background: linear-gradient(to left, hsl(0, 46%, 88%), hsl(0, 100%, 93%));
  filter: drop-shadow(0px 15px 10px #2f2f2f35);
}

form > img {
  width: 20px;
  height: 20px;

  z-index: 100;
  position: absolute;
  top: 30%;
  right: 33%;
}


@media screen and (max-width: 1100px) {
  .content {
    padding: 2rem 4rem;
  }
}

@media screen and (max-width: 880px) {
  .main {
    flex-direction: column;
  }
  .content {
    width: 100%;
    height: 100%;
    justify-content: start;
    padding: 5rem 4rem;
  }

  .hero-img, .logo-block {
    display: none;
  }

  .hero-mobile {
    display: block;
    width: 100%;
  }

  .logo-block-mobile {
    display: flex;
  }
  .hero-mobile > img {
    width: 100%;
  }
}

@media screen and (max-width: 440px) {
  .content {
    padding: 2rem 3rem;
  }
  .main {
    justify-content: center;
    text-align: center;
  }

  h1 {
    font-size: 40px;
    letter-spacing: 3px;
  }
}
